.Register {
  padding: 60px 0;
  text-align: center;
}

.Register-header {
  /* text-align: center; */
  font-size: 50px;
  font-weight: 300;
  font-family: Arial, Helvetica, sans-serif;
}

.form {
  margin-top: 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.form-input {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: fit-content;
}

label {
  font-size: 20px;
}

input {
  margin-top: 5px;
  min-width: 25em;
  height: 50px;
  padding: 0px 10px;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  background-color: #f3f3f3;
  border: 1;
  border-color: lightgray;
  border-radius: 4px;
  margin-bottom: 23px;
  transition: all 250ms ease-in-out;
}

.footer {
  margin-top: 1em;
}

.button {
  background-color: #444444;
  color: white;
  padding: 15px 50px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
}

.buttonshadow:hover {
  background-color: #444444; /* Green */
  border: none;
  color: white;
  padding: 15px 50px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  -webkit-transition-duration: 0.2s;
  transition-duration: 0.2s;
  box-shadow: 0 12px 16px 0 rgba(0, 0, 0, 0.24),
    0 17px 50px 0 rgba(0, 0, 0, 0.19);
}
